<template>
    <div class="product">
        {{ product }}
    </div>
</template>

<script>
    export default {
        name: "product-view",
        props: ["category", "product"],
        data() {
            product: null
        },
        mounted() {
            this.$axios.get("/products/" + this.category + "/" + this.product)
            .then((res) => {
                console.log(res)
                this.product = res.data.product
            })
        }
    }
</script>